	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"What is Art? What is Design?","depth":1}],"html":"<h1>What is Art? What is Design?</h1>\n<p>A shovel presented by Duchamp is considered art. A chair created by the Eames is considered design. The description of artist and designer have different contextual meanings, however there is undeniable overlap between the two fields.</p>\n<p>![Opening spread - Paragraphs on Conceptual Art](../assets/dou_opening spread.jpg)</p>\n<p><em>This is Art. This is Design.</em> tackles the blurred line between art and design through writings and images from renounced artists, designers and scholars. The information provided by the book is intended to be educational, and created in a way that allows readers to flow through the readings smoothly. </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Spread - Charles and Ray Eames \"\n        title=\"\"\n        src=\"/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-755ae.jpg\"\n        srcset=\"/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-f07fc.jpg 125w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-9d06f.jpg 250w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-755ae.jpg 500w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-63ca3.jpg 750w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-9b9a3.jpg 1000w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-6e031.jpg 1500w,\n/static/dou_spread1-6a6558e3079221552e41a15f4b32ae16-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Using academic texts and supportive images, the book highlights some of the important aspects to consider between art and design, for example education systems and transitional time periods. The information is separated into coloured, themed sections and the content alternates back and forth between art and design.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Spread - Black Mountain College pullquote\"\n        title=\"\"\n        src=\"/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-755ae.jpg\"\n        srcset=\"/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-f07fc.jpg 125w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-9d06f.jpg 250w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-755ae.jpg 500w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-63ca3.jpg 750w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-9b9a3.jpg 1000w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-6e031.jpg 1500w,\n/static/dou_spread2-ac072a20e174c3e7d54170b1fc3654c5-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>","frontmatter":{"title":"This is Art. This is Design.","graduate":"Tina Dou","category":"Book Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/spine-c64411021aeca7dea08d7c549d97a51c-a7a62.jpg","srcSet":"/static/spine-c64411021aeca7dea08d7c549d97a51c-5a1a9.jpg 375w,\n/static/spine-c64411021aeca7dea08d7c549d97a51c-0908c.jpg 750w,\n/static/spine-c64411021aeca7dea08d7c549d97a51c-a7a62.jpg 1500w,\n/static/spine-c64411021aeca7dea08d7c549d97a51c-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/this-is-art-this-is-design/","name":"/This is Art. This is Design./"}}

